<template>
  <div id="step-1" class="animate__animated animate__backInUp" v-loading="loading" element-loading-text="正在等待 NFC 校验..." element-loading-background="rgba(243, 241, 235, 0.5)" element-loading-spinner="el-icon-loading">
    <br />
    <br />
    <br />
    <v-img :src="jewelryBox" color="pink" class="animate__animated animate__bounceInDown"></v-img>
    <br />
    <br />
    <br />
    <br />
    <v-btn large block @click="nextStep()" style="color:#C7000B"> 请让您的手机背部靠近首饰盒... </v-btn>
  </div>
</template>

<script>
import jewelryBox from "../assets/images/jewelry-box.png";
export default {
  data: () => ({
    loading: false,
    jewelryBox,
  }),
  methods: {
    nextStep() {
      this.loading = true;
      const ndef = new NDEFReader();
      ndef.scan().then(() => {
        ndef.onreadingerror = (event) => {
          alert("【温馨提示】：NFC 读取错误！");
          this.loading = false;
        };
        ndef.onreading = (event) => {
          this.$router.push("/bind/step-2");
          this.loading = false;
        };
      }).catch(error => {
        alert("【温馨提示】：NFC 启动错误！");
        this.loading = false;
      });
    },
  },
  destroyed() {
    const ndef = new NDEFReader();
    ndef.scan().then(() => {
      ndef.onreadingerror = (event) => {
        console.log("onreadingerror");
      };
      ndef.onreading = (event) => {
        console.log("onreading");
      };
    }).catch(error => { });
  }
};
</script>

<style lang="scss" scoped>
.chip.v-icon {
  align-items: center;
  justify-content: center;
  font-size: 80vw;
}
</style>
